import { LOGIN, LOGOUT } from "../constant"

/*
    1.LoginReducer
    2.reducer
*/

const initState = { // 
    'login': false,
    'user_id': null,
    'username': null,
    'email': null,
    'name': null,
    'authority': null,
    'annotations': -1,
    'password': null,
} 

export default function loginReducer(preState=initState, action) {
    // action: type, data
    const {type, data} = action

    //type
    switch (type) {
        case LOGIN:
            return {
                'login': data.login,
                'user_id': data.user_id,
                'username': data.username,
                'email': data.email,
                'name': data.name,
                'authority': data.authority,
                'annotations': data.annotations,
                'password': data.password,
            }
        
        case LOGOUT:
            return initState

        default:   // 
            return preState
    }   
}